.about_navBlock {
  position: relative;
  background-color: white;
  height: 100%;
  max-width: 100%;
  width: 50%;
  position: absolute;
  left: -100%;
  z-index: 99;
  transition: 1s; }
  .about_navBlock .animate-link:hover .border-line {
    left: 100%;
    transition: 1s; }
  .about_navBlock_wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-top: 50px;
    padding-right: 25px; }
    .about_navBlock_wrapper_link {
      margin-bottom: 20px;
      font-family: MontserratRegular;
      font-size: 24px;
      font-weight: 100;
      position: relative;
      overflow: hidden;
      transition: 0.2s; }
      .about_navBlock_wrapper_link .border-line {
        position: absolute;
        display: block;
        bottom: 0;
        width: 100%;
        left: -100%;
        height: 2px;
        background-color: black; }
      .about_navBlock_wrapper_link .border-line::before {
        content: " "; }
    .about_navBlock_wrapper_language {
      margin-top: 50px;
      margin-bottom: 20px;
      font-family: MontserratRegular;
      font-size: 24px;
      font-weight: 100;
      position: relative;
      overflow: hidden;
      transition: 0.2s; }
      .about_navBlock_wrapper_language .border-line {
        position: absolute;
        display: block;
        bottom: 0;
        width: 100%;
        left: -100%;
        height: 2px;
        background-color: black; }
      .about_navBlock_wrapper_language .border-line::before {
        content: " "; }
    .about_navBlock_wrapper_theme {
      cursor: pointer;
      font-family: MontserratRegular;
      font-size: 24px;
      font-weight: 100;
      position: relative;
      overflow: hidden;
      transition: 0.2s; }
      .about_navBlock_wrapper_theme .border-line {
        position: absolute;
        display: block;
        bottom: 0;
        width: 100%;
        left: -100%;
        height: 2px;
        background-color: black; }
      .about_navBlock_wrapper_theme .border-line::before {
        content: " "; }

